import React, { useState, useRef } from 'react';
import '../app.scss';
import '../App.css'
import { Navbar } from 'react-bootstrap';
import { Nav } from 'react-bootstrap';
import { Form } from 'react-bootstrap';
import home from '../asserts/gicons/home.svg';
import book from '../asserts/gicons/book.svg';
import Logout from '../asserts/gicons/logout.svg';
import account from '../asserts/gicons/account.svg';
import { useAuth } from "../contexts/AuthContext";
import { useHistory, Link } from "react-router-dom";
import { Alert } from 'react-bootstrap';
import Search from '../asserts/gicons/search.svg';
import { db } from "../firebase";
import { Overlay } from 'react-bootstrap';
import { Popover } from 'react-bootstrap';


export default function Header() {
    const [error, setError] = useState("");
    const [Result, setResult] = useState([])
    const query = useRef("")
    const { currentUser, logout } = useAuth();
    const History = useHistory();
    var what, photo, log;
    const [show, setShow] = useState(false);
    const [target, setTarget] = useState(null);
    const ref = useRef(null);

    if (currentUser === null) {
        what = <p><Link to="/login">Login</Link></p>;
        photo = account;
        log = "/login"
    } else {
        what = <p>{currentUser.displayName}</p>;
        photo = currentUser.photoURL
        log = "/setting"
    }
    async function handleLogout() {
        setError("")
        try {
            await logout()
            History.push("/login")
        } catch {
            setError("Failed to log out")
        }
    }

    function result(event) {
        event.preventDefault();
        setResult([])
        db.collection("stories").where("name", "==", query.current.value).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
                setResult(arr => [...arr, doc.data().createdBy])
                //setResult([])
            })
        })
        setShow(!show);
        setTarget(event.target);
    }
    const res = Object.values(Result)
    return (
        <>
            <Navbar collapseOnSelect fixed="top" expand="lg" bg="light" variant="light" >
                <Navbar.Brand href="/">Consigliere</Navbar.Brand>
                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="mr-auto va">
                        <Nav.Link href="/"><img
                            src={home}
                            width="30"
                            height="30"
                            alt="home button"
                        /><p>Home</p></Nav.Link>
                        <Nav.Link href="/profile"><img
                            src={book}
                            width="30"
                            height="30"
                            alt="Book"
                        /><p>Book</p></Nav.Link>
                        <Nav.Link href={log}><img
                            src={photo}
                            width="30"
                            height="30"
                            alt="avatar"
                            id="avatar"
                        /></Nav.Link>
                        <Nav.Link href={log}><p className="name">{what}</p></Nav.Link>
                    </Nav>
                    {error && <Alert varient="danger">{error}</Alert>}

                    <Form onSubmit={result}>

                        <div className="searchBar" ref={ref}>
                            <Form.Control
                                size="sm"
                                type="text"
                                ref={query}
                                required
                                defaultValue="Find Someone"
                                className="search"
                            />

                            <Nav.Link onClick={result}>
                                <img
                                    src={Search}
                                    width="30"
                                    height="30"
                                    alt="Search button"
                                    type="submit"
                                /></Nav.Link>
                        </div>
                        <Overlay
                            show={show}
                            target={target}
                            placement='bottom'
                            container={ref.current}
                            containerPadding={-10}
                        >
                            <Popover id="popover-container">
                                {Result.map(({},i) => (
                                  <p>{Result[i]}</p>
                                ))}
                            </Popover>
                        </Overlay>
                    </Form>

                    <Nav>
                        <Nav.Link eventKey={2} onClick={handleLogout}><img
                            src={Logout}
                            width="30"
                            height="30"
                            className="d-inline-block align-top"
                            alt="Logout"
                        />
                        </Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Navbar >
            <hr />
        </>
    )
}
