import React, { useState, useEffect} from 'react'
import { useParams,useHistory } from 'react-router-dom'
import Card from 'react-bootstrap/Card';
import { db } from '../firebase';
import { useAuth } from "../contexts/AuthContext";
import HTMLFlipBook from "react-pageflip";
import {Helmet} from "react-helmet";
import Button from 'react-bootstrap/Button'
import '../App.css'
import '../app.scss'

export default function Friend() {
    let {id}  = useParams();
    const history = useHistory();
    const {currentUser} = useAuth();
    const [Data, setData] = useState("")
    const [Story, setStory] = useState([])
    const [Url, setUrl] = useState(null)
    const [loading,setLoading] = useState(false)
    const [followed , setFollowed] = useState(false)

    if(id === currentUser.uid){
        history.push("/profile")
    }

    useEffect(() => {
        db.collection("stories").where("createdBy" ,"==", id).onSnapshot(snapshot => (
            setStory(snapshot.docs.map(doc => doc.data()))
        ))
        db.collection("users").doc(id).collection("personalData").doc("cover").get().then((doc) => {
            if (doc.exists) {
                setUrl(doc.data().Url)
            }
        })
        db.collection("users").doc(id).collection('personalData').doc("info").get().then((doc) => {
            if(doc.exists) {
                var Data = doc.data();
                setData(Data)
            }else{
                history.push("/nomatch")
            }
        })
        db.collection("users").doc(currentUser.uid).collection("following").where("id", "==" , id).get().then(function(querySnapshot){
            querySnapshot.forEach(function(doc){
                if(doc.exists){
                    setFollowed(true)
                }else{
                    setFollowed(false)
                }
            })
            
        })
    }, [currentUser,id,followed,history])
    function handel_follow(){
        if(currentUser !== null){setLoading(true)
        db.collection("users").doc(currentUser.uid).collection("following").add({id}).then(() => {
            db.collection("users").doc(currentUser.uid).collection("personalData").doc("info").update({
                following: Number(Data.following) + 1
            });
        })
        db.collection("users").doc(id).collection("followed").add({id:currentUser.uid}).then(() => {
            db.collection("users").doc(id).collection("personalData").doc("info").update({
                followed: Number(Data.followed) + 1
            })
        })} else{
            history.push("/login")
        }
    }
    function handel_unfollow(){
        if(currentUser !== null){setLoading(true)
        db.collection("users").doc(currentUser.uid).collection("following").where("id", "==" , id).get().then(function(querySnapshot){
            querySnapshot.forEach(function(doc){
                doc.ref.delete();
            db.collection("users").doc(currentUser.uid).collection("personalData").doc("info").update({
                following: Number(Data.following) - 1    
            }) 
            })
        })
        db.collection("users").doc(id).collection("followed").where("id", "==" , currentUser.uid).then(() => {
            db.collection("users").doc(id).collection("personalData").doc("info").update({
                following: Number(Data.following) - 1
            })
        })}else{
            history.push("/login")
        }
    }
    const followButton = () => {
        if(followed !== true){
           return <Button disabled={loading} className="top_buttons" onClick={handel_follow}>follow</Button>
        }else{
            return <Button disabled={loading} className="top_buttons" onClick={handel_unfollow}>unfollow</Button>
        }
    }
    return (
        <>
        <Helmet>
            <title>{Data.name}</title>
        </Helmet>
        <Card>
            <div className="profile_card">
                <div className="profile_card__image">
                    <img src={Data.photoURL} alt="" className="profile_photo" />
                </div>
                <div className="profile_card__text">
                    <h3>{Data.name}</h3>
                    <p>{Data.discription}</p>
                </div>
                {followButton()}
            </div><hr />
            <div className="follower">
                <p>{Data.following}<h4>following</h4></p>
                <p>{Data.followed}<h4>followers</h4></p>
            </div>
        </Card>
        <Card >
        <Card.Body>
            <HTMLFlipBook
                drawShadow="false"
                width={550}
                height={900}
                size="stretch"
                minWidth={315}
                maxWidth={1100}
                minHeight={400}
                maxHeight={10000}
                maxShadowOpacity={0.5}
                showCover={true}
                mobileScrollSupport={true}
            >
                <div>{Url && <img src={Url} className="cover" alt="" />}</div>
                <div>Back-Cover</div>
                {Story.map(({ date, story, time, title }) => (
                    <div className="page">
                        <h2>{title}</h2>
                        <h4>{date} {time}</h4>
                        <p dangerouslySetInnerHTML={{ __html: story }}></p>
                    </div>
                ))}

            </HTMLFlipBook>
        </Card.Body>
    </Card>
    </>
    )
}
