import React, { useRef, useState, useEffect } from 'react'
import { useAuth } from "../contexts/AuthContext";
import { db } from '../firebase';
import { useSprings, animated } from '@react-spring/web'
import useMeasure from 'react-use-measure'
import { useDrag } from 'react-use-gesture'
import clamp from 'lodash.clamp'
import Card from 'react-bootstrap/Card';
import { Button } from 'react-bootstrap';
import styles from './styles.module.css'
import Menu from "../asserts/gicons/buttons.svg"
import TTS from './TTS';


export default function Viewpager() {
    const { currentUser } = useAuth()
    const index = useRef(0)
    const [ref, { width }] = useMeasure()
    const [story, setStory] = useState([]);
    const [show, setShow] = useState('none');
    const [voices, speak] = TTS()
    const [currentVoice, setCurrentVoice] = useState()



    const [props, api] = useSprings(
        story.length,
        i => ({
            x: i * width,
            scale: width === 0 ? 0 : 1,
            display: 'block',
        }),
        [width]
    )
    const bind = useDrag(({ active, movement: [mx], direction: [xDir], distance, cancel }) => {
        if (active && distance > width / 2) {
            index.current = clamp(index.current + (xDir > 0 ? -1 : 1), 0, story.length - 1)
            cancel()
        }
        api.start(i => {
            if (i < index.current - 1 || i > index.current + 1) return { display: 'none' }
            const x = (i - index.current) * width + (active ? mx : 0)
            const scale = active ? 1 - distance / width / 2 : 1
            return { x, scale, display: 'block' }
        })
    })



    // to get DATA
    useEffect(() => {

        function fetch() {
            db.collection("stories").onSnapshot((snapshot) => {
                setStory(snapshot.docs.map(doc => doc.data()))
            })
        }
        return fetch()
    }, [currentUser])

    //TTS
    useEffect(() => {
        if (!currentVoice) {
            setCurrentVoice(voices.filter(v => v.default)[0] || voices[0]);
        }
    }, [voices, currentVoice])

    const handleVoiceChange = e => {
        setCurrentVoice(voices.filter(v => v.name === e.target.value)[0]);
    }

    const handelSpeak = async (text) => {
        const title = "Title of the story." + text.title
        await speak(title, currentVoice)
        let tmp = document.createElement("DIV")
        tmp.innerHTML = text.story
        var Story = "story." + tmp.textContent
        speak(Story, currentVoice)

    }

    const handelShow = () => {
        if (show === 'none') {
            setShow('flex')

        } else {
            setShow('none')
        }
    }



    return (
        <>
            <div ref={ref} className={styles.wrapper} >
                {
                    props.map(({ x, display, scale }, i) => (
                        <>
                            <animated.div className={styles.page} {...bind()} key={i} style={{ display, x }}>
                                <animated.div style={{ scale }} key={i} className="bg">
                                    <Card className="page" style={{ width }} >
                                        <a href={"/user/" + story[i].createdBy} className="a" >
                                            <Card.Header className="cardHeader">
                                                <img src={story[i].photo} alt="" className="cardPhoto" />
                                                <h3 style={{ marginTop: 10 }}>{story[i].name}</h3>
                                            </Card.Header>
                                        </a>
                                        <Card.Header className="cardHeader1">
                                            <div>
                                                <h2>{story[i].title}</h2>
                                                <h4>{story[i].time} {story[i].date}</h4>
                                            </div>
                                            <img src={Menu} alt="" className="Menu" onClick={handelShow} />

                                            <div className="pop" style={{ display: show }}>
                                                <div className="modal-content">
                                                    <span className="close" onClick={handelShow}>&times;</span>
                                                    <form className="contain" >
                                                        <div className="select">
                                                            <select value={currentVoice ? currentVoice.name : ''} onChange={handleVoiceChange}>
                                                                {voices.map(v => (
                                                                    <option value={v.name}>{`${v.name}`}</option>
                                                                ))}
                                                            </select>
                                                        </div>
                                                    </form>
                                                    <Button className="narrate" onClick={() => { handelSpeak(story[i]) }}>Narrate</Button>
                                                </div>
                                            </div>
                                        </Card.Header>
                                        <Card.Body>
                                            <div id="story">
                                                <p dangerouslySetInnerHTML={{ __html: story[i].story }} />
                                            </div>
                                        </Card.Body>
                                    </Card>
                                </animated.div>
                            </animated.div>
                        </>
                    ))}
            </div>
        </>
    )
}
