import React, { useRef, useState, useEffect } from 'react';
import { useAuth } from "../contexts/AuthContext"
import Card from 'react-bootstrap/Card';
import Form from 'react-bootstrap/Form';
import FormLabel from 'react-bootstrap/FormLabel';
import Accordion from 'react-bootstrap/Accordion';
import Alert from 'react-bootstrap/Alert';
import { Button } from 'react-bootstrap';
import { useHistory } from "react-router-dom";
import edit from '../asserts/gicons/edit.svg'
import 'reactjs-popup/dist/index.css';
import { storage } from '../firebase'
import ReactCrop from 'react-image-crop';
import 'react-image-crop/dist/ReactCrop.css';
import Popup from 'reactjs-popup';
import { db } from '../firebase';
import {Helmet} from "react-helmet";


export default function Settings() {
    // Setting up all Constante neccesory
    const nameRef = useRef()
    const discriptionRef = useRef("")
    var newDate = new Date()
    const [discription, setdiscription] = useState("")
    var date = newDate.getDate(), month = newDate.getMonth(), year = newDate.getFullYear()
    const DOB = useRef(year, "-", month, "-", date)
    // firebase userdata
    const { currentUser, updateDisplayName, updatePhotoURL } = useAuth()

    const [error, setError] = useState("")
    const [loading, setLoading] = useState(false)
    const history = useHistory()
    // Profile Photo
    const [file, setFile] = useState(null);
    const [url, setUrl] = useState({
        files: null
    })
    // user info variable
    var user = db.collection("users").doc(currentUser.uid).collection('personalData')

    // for Crop
    const [image, setImage] = useState(null)
    const [crop, setCrop] = useState({ unit: '%', width: 30, height: 30 })
    const [open, setOpen] = useState(false)
    // end



    if (currentUser === null) {
        history.push("/")
    }


    function handleChange(e) {
        setFile(e.target.files[0]);
        setUrl({
            files: URL.createObjectURL(e.target.files[0])
        });
        setOpen(true)
    }

    function handleUpload(e) {
        e.preventDefault();
        if (file !== null) {
            const promises = []
            const uploadTask = storage.ref(`/images/${currentUser.uid}`).put(file);
            uploadTask.on("state_changed", console.log, console.error, () => {
                storage
                    .ref("images")
                    .child(currentUser.uid)
                    .getDownloadURL()
                    .then((url) => {
                        promises.push(updatePhotoURL(url))
                    });
            });

        } else {
            setError("Unexpected Error Occured")
        }

    }

    function getCroppedImg() {
        const canvas = document.createElement('canvas');
        const scaleX = image.naturalWidth / image.width;
        const scaleY = image.naturalHeight / image.height;
        canvas.width = crop.width;
        canvas.height = crop.height;
        const ctx = canvas.getContext('2d');

        ctx.drawImage(
            image,
            crop.x * scaleX,
            crop.y * scaleY,
            crop.width * scaleX,
            crop.height * scaleY,
            0,
            0,
            crop.width,
            crop.height,
        );
        const base64image = canvas.toDataURL('image/jpge')
        setUrl({
            files: base64image
        });
        canvas.toBlob(blob => {
            setFile(blob)
        })
        setOpen(false)
    }




    function handelSubmit(e) {
        e.preventDefault()
        const promises = []
        setLoading(true)
        setError("")

        if (nameRef !== currentUser.displayName) {
            promises.push(updateDisplayName(nameRef.current.value))
            db.collection("stories").where("createdBy", "==", currentUser.uid).get().then((snapshot) => {
                snapshot.docs.forEach( doc => {
                    db.collection("stories").doc(doc.id).update({
                        name:nameRef.current.value
                    })
                })
            })
        } else {
            setError("Name is same as old one")
        }

        Promise.all(promises)
            .then(() => {
                history.push("/")
            })
            .catch(() => {
                setError("Failed to update Account")
            })
            .finally(() => {
                setLoading(false)
            })

    }

    function userInfo() {
        user.doc('info').set({
            name: currentUser.displayName,
            photoURL: currentUser.photoURL,
            discription: discriptionRef.current.value,
            Email: currentUser.email,
            DOB: DOB.current.value
        }).then(() => {
            console.log("Document Succesfully Upload!")
        }).catch((error) => {
            console.log(error)
        })
    }
    useEffect(() => {
        function getValue() {
            user.doc('info').get().then((doc) => {
                if (doc.exists) {
                    var data = doc.data();
                    setdiscription(data)
                }
            })
        }
        getValue()
    }, [user])


    return (
        <>
        <Helmet>
            <title>Settings</title>
        </Helmet>
            {error && <Alert variant="danger">{error}</Alert>}
            <Accordion defaultActiveKey="0">
                <Card>
                    <Accordion.Toggle as={Card.Header} eventKey="0">

                        <h2>Profile</h2>

                    </Accordion.Toggle>
                    <Accordion.Collapse eventKey="0">
                        <Card.Body>
                            <Form onSubmit={function (event) { handleUpload(event); handelSubmit(event); userInfo() }}>
                                <Form.Group>
                                    <div className="profile">
                                        <img src={url.files || currentUser.photoURL} height="200px" width="200px" className="profilephoto" alt="" />
                                        <FormLabel htmlFor="fileUpload"><img src={edit} alt="" /></FormLabel>
                                        <Form.File accept="image/*" type="file" id="fileUpload" onChange={handleChange} hidden />
                                        {url && <Popup open={open} modal nested>
                                            <div className="image-fluid">
                                                <ReactCrop src={url.files} onImageLoaded={setImage} crop={crop} onChange={setCrop} />
                                                <Button className="btn" onClick={getCroppedImg}>Save</Button>
                                            </div>
                                        </Popup>}

                                    </div>
                                </Form.Group>
                                <Form.Group>
                                    <Form.Label>Display Name</Form.Label>
                                    <Form.Control type="text" ref={nameRef} required defaultValue={currentUser.displayName} />
                                    <Form.Text>Set Writteres Name</Form.Text>
                                    <Form.Label>Epigraph</Form.Label>
                                    <Form.Control type='text' minLength="20" ref={discriptionRef} defaultValue={discription.discription} required />
                                    <Form.Text>a short phrase or sentence on your book</Form.Text>
                                    <br />
                                    <Button disabled={loading} type="submit">Save</Button>
                                </Form.Group>
                            </Form>
                        </Card.Body>
                    </Accordion.Collapse>

                    <Accordion.Toggle as={Card.Header} eventKey="1">
                        <h2>Account</h2>
                    </Accordion.Toggle>
                    <Accordion.Collapse eventKey="1">
                        <Card.Body>
                            <Form>
                                <Form.Label>Date of Birth</Form.Label>
                                <Form.Control type='Date' ref={DOB} defaultValue={discription.DOB} max={year + "-" + month + "-" + date} required />
                                <Form.Label>Email</Form.Label>
                                <Form.Control type="email" defaultValue={currentUser.email}></Form.Control>
                                <Button disabled={loading}>Save</Button>
                            </Form>
                        </Card.Body>
                    </Accordion.Collapse>
                </Card>
            </Accordion>
        </>
    )
}
