import React, { useRef, useState } from "react"
import { Form, Button, Card, Alert } from "react-bootstrap"
import { useAuth } from "../contexts/AuthContext"
import { Link, useHistory } from "react-router-dom"
import { GLogin, Flogin } from '../firebase'
import {Helmet} from "react-helmet";

export default function Login() {
  const emailRef = useRef()
  const passwordRef = useRef()
  const { login, currentUser } = useAuth()
  const [error, setError] = useState("")
  const [loading, setLoading] = useState(false)
  const history = useHistory()


  async function handleSubmit(e) {
    e.preventDefault()

    try {
      setError("")
      setLoading(true)
      await login(emailRef.current.value, passwordRef.current.value)
      history.push("/")
    } catch {
      setError("Incorrect Password or Email Id")
    }

    setLoading(false)
  }
  if (currentUser !== null) {
    history.push("/")
  }

  function Glogin(e) {
    e.preventDefault()
    setError("")
    try{
      GLogin()
    }catch{
      setError("Unexpected error occured")
    }
    
  }
  function FLogin(e) {
    e.preventDefault()
    setError("")
    try{
      Flogin()
    }catch{
      setError("Account already logged in with Google")
    }
    
  }

  return (
    <>
    <Helmet>
      <title>
        login
      </title>
    </Helmet>
      <Card>
        <Card.Body>
          <h2 className="text-center mb-4">Log In</h2>
          {error && <Alert variant="danger">{error}</Alert>}
          <Form onSubmit={handleSubmit}>
            <Form.Group id="email">
              <Form.Label>Email</Form.Label>
              <Form.Control type="email" ref={emailRef} required />
            </Form.Group>
            <Form.Group id="password">
              <Form.Label>Password</Form.Label>
              <Form.Control type="password" ref={passwordRef} pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}" required />
            </Form.Group>
            <Button disabled={loading} className="w-100" type="submit">
              Log In
            </Button>
          </Form>
          <div className="w-100 text-center mt-3">
            <Link to="/forget">Forgot Password?</Link>
          </div>
          <div className="separator">
            <div className="line"></div>
            <h6>or</h6>
            <div className="line"></div>
          </div>
          <div className="buttons">
            <div className="gbutton">
              <div className="google-btn" onClick={Glogin}>
                <div className="google-icon-wrapper"><img className="google-icon" src="https://upload.wikimedia.org/wikipedia/commons/5/53/Google_%22G%22_Logo.svg" alt="" /></div>
                <p className="btn-text"
                ><b>Login with Google</b></p>
              </div>
            </div>
            <div className="fbutton">
              <div className="face-btn" onClick={FLogin}>
                <div className="Face-icon-wrapper"><img className="face-icon" src="https://upload.wikimedia.org/wikipedia/commons/f/fb/Facebook_icon_2013.svg" alt="" /></div>
                <p className="btn-text"
                ><b>Login with Facebook</b></p>
              </div>
            </div>
          </div>
        </Card.Body>
      </Card>
      <div className="w-100 text-center mt-2">
        Need an account? <Link to="/signup">Sign Up</Link>
      </div>
    </>
  )
}