import React, { useRef, useState } from "react";
import { Form, Button, Card, Alert } from "react-bootstrap";
import { Link, useHistory } from "react-router-dom"
import { useAuth } from '../contexts/AuthContext';
import { GLogin, Flogin } from '../firebase'
import {Helmet} from "react-helmet";
import '../app.scss'


export default function GoogleLogin() {
  const emailRef = useRef()
  const passwordRef = useRef()
  const passwordConfirmRef = useRef()
  const { signup, currentUser } = useAuth()
  const [error, setError] = useState('')
  const [loading, setLoading] = useState(false)
  const history = useHistory()

  async function handleSubmit(e) {
    e.preventDefault()

    if (passwordRef.current.value !== passwordConfirmRef.current.value) {
      return setError("Passwords do not match")
    }
    try {
      setError("")
      await signup(emailRef.current.value, passwordRef.current.value)
      history.push("/setting")
    } catch {
      setError("E-Mail already exist.use another account or Log in")
    }
    setLoading(false)
  }


  if (currentUser !== null) {
    history.push("/")
  }

  function login(e) {
    e.preventDefault()
    setError("")
    try{
      GLogin()
    }catch{
      setError("Unexpected error occured")
    }
    
  }
  function FLogin(e) {
    e.preventDefault()
    setError("")
    try{
      Flogin()
    }catch{
      setError("Account already logged in with Google")
    }
    
  }


  return (
    <>
    <Helmet>
      <title>Signup</title>
    </Helmet>
      <Card>
        <Card.Body>
          <h2>Signup</h2>
          {error &&
            <Alert variant="danger" fade={false} >{error}</Alert>
          }
          <Form onSubmit={handleSubmit}>

            <Form.Group id="email">
              <Form.Label>Email</Form.Label>
              <Form.Control type="email" ref={emailRef} placeholder="Email" pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$" required />
            </Form.Group>
            <Form.Group id="password">
              <Form.Label>password</Form.Label>
              <Form.Control type="password" ref={passwordRef} placeholder="Password" pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}" required />
            </Form.Group>
            <Form.Group id="password-confirm">
              <Form.Label>Password Confirmation</Form.Label>
              <Form.Control type="password" ref={passwordConfirmRef} placeholder="Confirm Password" pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}" required />
            </Form.Group>
            <Button disabled={loading} className="w-100">Submit</Button>
            <div className="separator">
              <div className="line"></div>
              <h6>or</h6>
              <div className="line"></div>
            </div>
            <div className="buttons">
              <div className="gbutton">
                <div className="google-btn" onClick={login}>
                  <div className="google-icon-wrapper"><img className="google-icon" src="https://upload.wikimedia.org/wikipedia/commons/5/53/Google_%22G%22_Logo.svg" alt="" /></div>
                  <p className="btn-text"
                  ><b>Sign in with Google</b></p>
                </div>
              </div>
              <div className="fbutton">
                <div className="face-btn" onClick={FLogin}>
                  <div className="Face-icon-wrapper"><img className="face-icon" src="https://upload.wikimedia.org/wikipedia/commons/f/fb/Facebook_icon_2013.svg" alt="" /></div>
                  <p className="btn-text"
                  ><b>Sign in with Facebook</b></p>
                </div>
              </div>
            </div>
          </Form>
        </Card.Body>
      </Card>
      <div className="w-100 text-center mt-2">
        Already have an account? <Link to="/login">Log In</Link>
      </div>
    </>
  )

}

