import React, { useRef, useState, useEffect } from 'react'
import { useAuth } from "../contexts/AuthContext";
import { useHistory } from "react-router-dom";
import Card from 'react-bootstrap/Card';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';
import Tabs from 'react-bootstrap/Tabs';
import Tab from 'react-bootstrap/Tab';
import ReactQuill from 'react-quill';
import {Helmet} from "react-helmet";
import 'react-quill/dist/quill.snow.css';
import { db } from '../firebase';
import { storage } from '../firebase'

export default function Mybook() {
  const { currentUser } = useAuth()
  const history = useHistory()
  const Titleref = useRef("")
  const [error, setError] = useState("")
  const [text, setText] = useState("")
  const [Url, setUrl] = useState(null)
  const [Loading, setLoading] = useState(false)
  const [file, setFile] = useState(null);

  var today = new Date(), date = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
  var time = today.getHours() + ':' + today.getMinutes() + ':' + today.getSeconds();

  if (currentUser === null) {
    history.push("/")
  }

  const handelChange = (text) => {
    setText(text)
  }

  const CustomToolbar = () => (
    <div id="toolbar">

    </div>
  )

  function handelUpload(e) {
    e.preventDefault();
    setLoading(true)
    db.collection("stories").doc().set({
      title: Titleref.current.value,
      date: date,
      time: time,
      story: text,
      createdBy:currentUser.uid,
      like:0,
      name:currentUser.displayName,
      photo:currentUser.photoURL
    }).then(() => {
      history.push("/")
    }).catch((error) => {
      setError(error)
    })

  }
  function imageUpload(e) {
    e.preventDefault();
    setFile(e.target.files[0]);
    setUrl({
      files: URL.createObjectURL(e.target.files[0])
    });
  }

  function UploadCover(e) {
    e.preventDefault();
    setLoading(true)
    if (file !== null) {
      const uploadtask = storage.ref(`/cover/${currentUser.uid}`).put(file);
      uploadtask.on("state_changed", () => {
        storage
          .ref("cover")
          .child(currentUser.uid)
          .getDownloadURL()
          .then((url) => {
            db.collection("users").doc(currentUser.uid).collection("personalData").doc("cover").set({
              Url: url
            })
            setLoading(false)
          })
      })
    } else {
      setError("Server Error")
    }

  }

  useEffect(() => {
    db.collection("users").doc(currentUser.uid).collection("personalData").doc("cover").get().then((doc) => {
      if (doc.exists) {
        var data = doc.data();
        setUrl(data.Url)
      } else {
        setError("No cover found")
      }
    })
  }, [currentUser])

  return (
    <>
      <Helmet>
        <title>
          My book
        </title>
      </Helmet>

      <Card >
        {error && <Alert variant="danger">{error}</Alert>}
        <Card.Body>
          <Tabs defaultActiveKey="scribe" id="uncontrolled-tab">
            <Tab eventKey="scribe" title="Sribe"><hr />
              <Form onSubmit={handelUpload}>
                <Form.Label><h4>Title</h4></Form.Label>
                <Form.Control ref={Titleref} type="Text" />
                <Form.Label><h4>Story</h4></Form.Label>
                <CustomToolbar />
                <ReactQuill value={text} onChange={handelChange.bind(text)} />
                <br />
                <Button disabled={Loading} type="submit">Post</Button>
              </Form>
            </Tab>
            <Tab eventKey="changeCover" title="Book Cover"><hr />
              <Form.File accept="image/*" type="file" id="fileUpload" onChange={imageUpload} /><hr />
              <div>
                {Url && <img src={Url} alt="" className="cover" />}

                <Button disabled={Loading} onClick={UploadCover}>Save</Button>
              </div>
            </Tab>
          </Tabs>
        </Card.Body>
      </Card>
    </>
  )
}
