import React, { useState, useEffect, useRef } from 'react';
import Card from 'react-bootstrap/Card';
import { db } from '../firebase';
import { useAuth } from "../contexts/AuthContext";
import { useHistory } from "react-router-dom";
import HTMLFlipBook from "react-pageflip";
import Button from 'react-bootstrap/Button'
import {Helmet} from "react-helmet";
import '../App.css'
import '../app.scss'


export default function Pro() {

    // global variable
    const { currentUser } = useAuth()
    const history = useHistory()
    const book = useRef()
    const [Url, setUrl] = useState("")
    const [discription, setdiscription] = useState("This is dicription")
    //cheak for log in
    if (currentUser === null) {
        history.push("/")
    }
    //useState for  story fetching
    const [story, setStory] = useState([]);
    //getData once
    useEffect(() => {
        db.collection("stories").where("createdBy" ,"==", currentUser.uid).onSnapshot(snapshot => (
            setStory(snapshot.docs.map(doc => doc.data()))
        ))
        db.collection("users").doc(currentUser.uid).collection("personalData").doc("cover").get().then((doc) => {
            if (doc.exists) {
                setUrl(doc.data().Url)
            }
        })
        db.collection("users").doc(currentUser.uid).collection('personalData').doc("info").get().then((doc) => {
            if(doc.exists) {
                var Data = doc.data();
                setdiscription(Data)
            }
        })
    }, [currentUser]);

    


    return (
        <>
        <Helmet>
            <title>
                {currentUser.displayName}
            </title>
        </Helmet>

            <Card>
                <div className="profile_card">
                    <div className="profile_card__image">
                        <img src={currentUser.photoURL} alt="" className="profile_photo" />
                    </div>
                    <div className="profile_card__text">
                        <h3>{currentUser.displayName}</h3>
                        <p>{discription.discription}</p>
                    </div>
                    <Button className="top_buttons" onClick={() => {history.push('/book')}}>New Story</Button>
                    <Button className="top_buttons" onClick={() => {history.push('/setting')}}>Edit Profile</Button>
                </div><hr/>
                <div className="follower">
                    <p>{discription.following}<h4>following</h4></p>
                    <p>{discription.followed}<h4>followers</h4></p>
                </div>
            </Card>
            <Card >
                <Card.Body>
                    <HTMLFlipBook
                        drawShadow="false"
                        width={550}
                        height={900}
                        size="stretch"
                        ref={book}
                        minWidth={315}
                        maxWidth={1100}
                        minHeight={400}
                        maxHeight={10000}
                        maxShadowOpacity={0.5}
                        showCover={true}
                        mobileScrollSupport={true}
                    >
                        <div>{Url && <img src={Url} className="cover" alt="" />}</div>
                        <div>Front-Back</div>
                        {story.map(({ date, story, time, title }) => (
                            <div className="page">
                                <h2>{title}</h2>
                                <h4>{date} {time}</h4>
                                <p dangerouslySetInnerHTML={{ __html: story }}></p>
                            </div>
                        ))}

                    </HTMLFlipBook>
                </Card.Body>
            </Card>

        </>
    )
}

