import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import './app.scss';
import Header from './componets/header';
import Card from './componets/card';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Book from './componets/mybook';
import Signup from './componets/signup';
import Login from './componets/login';
import ForgetPassword from './componets/forgetpassword';
import Settings from './componets/Settings';
import Profile from './componets/profile.jsx'
import NoMatch from './componets/NoMatch'
import { AuthProvider } from "./contexts/AuthContext";
import Friend from './componets/friend';



export default function App() {
  return (

    <AuthProvider>
        <Router>
          <Header />
          <Switch>
            <Route path="/" exact component={Card} />
            <Route path="/book" exact component={Book} />
            <Route path="/Signup" exact component={Signup} />
            <Route path="/Login" exact component={Login} />
            <Route path="/forget" exact component={ForgetPassword} />
            <Route path="/setting" exact component={Settings} />
            <Route path="/profile" exact component={Profile} />
            <Route path="/user/:id" component={Friend}/>
            <Route path="*" component={NoMatch} />
          </Switch>
        </Router>
    </AuthProvider>
  )
}

